body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
margin: 0;
padding: 0;
font-family: sans-serif;
background: #eeba4a;
background: linear-gradient(300deg ,#ac2442 30%,#4f0a64 90%);
height: 100vh;
}
.wrapper{
 
  max-width: 450px;
  text-align: center;
  margin: 0 auto;
  min-height: 70vh;
 margin-top: 60px;
 
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button{
  font-size: 2rem;
  cursor: pointer;
  border: 1px solid black;
}
button :hover{
  font-size: xx-large;
}
.operation button{
  background: #948c19;
  width: 100px;
}
.digits button{
  background-color: rgb(184, 41, 96);
  font-weight: 800px;
  
}
.modifiers button{
  background: #58a113;
  text-transform: uppercase;
}
.wrapper,.subgrid{
 display: grid;
}
.wrapper{
  grid-template-areas: 
  "total total total total"
  "modif modif modif  oper"
  "digit digit digit  oper"
  "digit digit digit  oper"
  "digit digit digit  oper"
  "digit digit digit  oper";
  grid-auto-columns: 1fr;

}
.showinput{
  grid-area: total;
}
.modifiers
{
  grid-area: modif;
}
.modifiers{
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  text-transform: uppercase;
}
.operation{
  grid-area: oper;
}
.digits{
  grid-area: digit;
  color: aliceblue;
}
.digits{
  display: flex;
  flex-wrap: wrap;
  
}
.digits button{
  flex: 1 0 26%;
}
.showinput{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: xx-large;
  font-weight: bolder;
  color: aliceblue;
}
